export default {
  computed: {
    getData: function getData() {
      return this.cardList.slice((this.page - 1) * this.totalPages, (this.page - 1) * this.totalPages + this.totalPages + 1);
    }
  },
  props: {
    theme_color: {
      type: String,
      default: '#10731f'
    },
    cardList: {
      type: Array,
      default: []
    },
    cardClickEvent: {
      type: String,
      default: ""
    }
  },
  data: function data() {
    return {
      page: 1,
      currentPage: 1,
      nextPage: null,
      totalPages: 5
    };
  },
  methods: {
    hoverOver: function hoverOver(index) {
      this['about_heading_color_' + index] = this.theme_color;
      this['about_heading_' + index] = ['animated', 'bounceIn'];
    },
    hoverOutTimeout: function hoverOutTimeout(index) {
      var _this = this;
      this['about_heading_color_' + index] = '#424242';
      setTimeout(function () {
        _this['about_heading_' + index] = [];
      }, 1000);
    },
    cardClickHandler: function cardClickHandler(card) {
      this.$emit(card.name, card);
    },
    viewItem: function viewItem(selectedItem) {
      this.$emit("updateItem", selectedItem);
    }
  },
  created: function created() {
    var context = this;
    context.totalPages = Math.ceil(context.cardList.length / 3);
    if (context.totalPages <= 0) {
      context.totalPages = 1;
    }
  }
};