import io from "socket.io-client";
export default {
  data: function data() {
    return {
      toId: "",
      fromId: "",
      fromUserName: "",
      toUserName: "",
      socket: null,
      serverUrl: "http://localhost:4000",
      newMessage: null,
      messages: [],
      typing: false,
      username: null,
      ready: false,
      info: [],
      connections: 0
    };
  },
  watch: {
    newMessage: function newMessage(value) {
      var context = this;
      value ? context.socket.emit('typing', context.fromUserName) : context.socket.emit('stopTyping');
    }
  },
  methods: {
    send: function send() {
      var context = this;
      var msg = {
        time: Date.now(),
        fromId: context.fromId,
        toId: context.toId,
        fromUserName: context.fromUserName,
        toUserName: context.toUserName,
        message: context.newMessage,
        images: [],
        //files urls
        requestId: "",
        requestTitle: ""
      };
      context.messages.push(msg);
      context.socket.emit('chat-message', msg);
      context.newMessage = null;
    },
    connectToWebsocket: function connectToWebsocket() {
      var context = this;
      context.socket = io(context.serverUrl, {
        transports: ["websocket"]
      });
    }
  },
  created: function created() {
    var context = this;
    context.connectToWebsocket();
    context.socket.on('chat-message', function (data) {
      context.messages.push({
        time: data.id,
        fromId: data.fromId,
        toId: data.toId,
        fromUserName: data.fromUserName,
        toUserName: data.toUserName,
        message: data.message
      });
    });
    context.socket.on('typing', function (data) {
      console.log("data: ", data);
      context.typing = data;
    });
    context.socket.on('stopTyping', function () {
      context.typing = false;
    });

    //+91 7328323606
  }
};