import search_Item from "./view_models/search_item";
export default {
  props: {
    search_Item: {
      type: Object,
      default: search_Item
    }
  },
  methods: {
    onQSelectItemValueChanged: function onQSelectItemValueChanged() {
      var context = this;
      this.$emit(context.search_Item.qSelect.actionName, context.qSelect);
    },
    onRemove: function onRemove() {
      var context = this;
      this.$emit("onRemove", context.search_Item);
    }
  }
};