export default {
  name: 'savingImportDataDialog',
  computed: {
    dialogStyle: function dialogStyle() {
      return {
        maxHeight: "500px",
        maxWidth: "500px"
      };
    }
  },
  props: {
    title: {
      type: String,
      required: true
    },
    message: {
      type: String,
      default: ''
    },
    cancelEvent: {
      type: String,
      default: ''
    },
    okayEvent: {
      type: String,
      default: ''
    }
  },
  data: function data() {
    return {
      dense: false
    };
  },
  methods: {
    cancelMessageBox: function cancelMessageBox() {
      var context = this;
      this.$emit(context.cancelEvent, context.title);
    },
    oKMessageBox: function oKMessageBox() {
      var context = this;
      this.$emit(context.okayEvent, context.title);
    }
  }
};