    msExcelFileImportMethod: function msExcelFileImportMethod() {
      var context = this;
      var workSheetsNames = [];
      var workSheetsData = [];
      var reader = new FileReader();
      this.$store.commit("authenticationStore/setShowSpinner", true);
      reader.onload = function (ev) {
        try {
          var data = ev.target.result;
          var XLSX = xlsx;
          var workbook = XLSX.read(data, {
            type: "binary",
            cellDates: true,
            cellNF: false,
            cellText: false
          });
          var nCount = workbook.SheetNames.length;
          var i = 0;
          var j = 0;
          workSheetsNames = [];
          workSheetsData = [];
          for (j = 0; j < nCount; j++) {
            var wsname = workbook.SheetNames[j];
            workSheetsNames.push(wsname);
            var ws = XLSX.utils.sheet_to_json(workbook.Sheets[wsname], {
              defval: ""
            });
            var excellist = [];
            for (i = 0; i < ws.length; i++) {
              excellist.push(ws[i]);
            }
            workSheetsData.push(excellist);
          }
          context.setImportFormData(workSheetsNames, workSheetsData);
          context.filesReadingCompleted(workSheetsNames, workSheetsData);
        } catch (e) {
          console.log("e: ", e);
          alert("Read failure!");
          context.filesReadingCompleted(workSheetsNames, workSheetsData);
        }
      };
      if (context.selectedFile) {
        if (/\.(xls|xlsx)$/i.test(context.selectedFile.name)) {
          //readAsDataURL
          //readAsBinaryString
          reader.readAsBinaryString(context.selectedFile);
        } else {
          alert("Wrong Microsoft Excel Format. Only supports .xls or .xlsx");
        }
      }
    },
