  mounted: function mounted() {
    console.log("Hello This is created!");
    //this.renderMath();
  },
  created: function created() {
    var context = this;
    context.form.clearQSelects();
    context.setImportFormData(context.workSheetsNames, context.workSheetsData);
    context.setDataTypes();
    context.applicationColumns = context._applicationColumns.map(function (row) {
      return _objectSpread(_objectSpread({}, row), {}, {
        isToggle: true,
        qToggle: {
          name: true,
          label: ""
        }
      });
    });
    context.worksheetColumns = context.applicationColumns.map(function (row, index) {
      return _objectSpread(_objectSpread({}, row), {}, {
        isToggle: false,
        i: "Worksheet Column ".concat(index + 1)
      });
    });
    context.tableVM.columnsOriginal = context.applicationColumns.map(function (row) {
      return {
        data: _objectSpread({}, row),
        name: "".concat(row.value),
        label: "".concat(row.value.toUpperCase()),
        field: "",
        align: "left",
        type: ""
      };
    });
    var methods = ["Method 1", "Method 2"];
    context.importMethodForm.qSelects[0].list = methods.map(function (row, i) {
      return {
        id: i,
        type: row,
        value: row,
        label: row
      };
    });
    console.log("importMethodForm: ", context.importMethodForm);
    if (context.importMethodForm.qSelects[0].list.length > 0) {
      context.importMethodForm.qSelects[0].value = context.importMethodForm.qSelects[0].list[0].id;
    }

    //console.log("context.applicationColumns: ", context.applicationColumns)
  }
};