import { printableRoutes } from "./printable-routes";
export default {
  computed: {
    activeRoute: function activeRoute() {
      var activeRoute = this.$store.getters["authenticationStore/activeRoute"];
      return printableRoutes[activeRoute].route;
    }
  },
  components: {
    AssessmentLanding: AssessmentLanding,
    ClassroomLanding: ClassroomLanding,
    ResultsLanding: ResultsLanding,
    StudentResultsLanding: StudentResultsLanding,
    StaffLanding: StaffLanding,
    StudentLanding: StudentLanding,
    SubjectLanding: SubjectLanding,
