import form from "./FormVM.js";
import EditableTable from "../Tables/EditableTable.vue";
import ContextMenu from "../Menus/context-menu.vue";
import ContextMenuItem from "../Menus/context-menu-item.vue";
import { getLineWidth } from 'plotly.js-dist';
export default {
  computed: {
    setIsResponsive: function setIsResponsive() {
      var width = window.innerWidth;
      if (width < 700) return true;else return false;
    },
    listBoxStyle: function listBoxStyle() {
      var width = window.innerWidth - 20;
      return {
        //height: `100vh`,
        width: "".concat(width, "px")
      };
    }
  },
  components: {
    EditableTable: EditableTable,
    ContextMenu: ContextMenu,
    ContextMenuItem: ContextMenuItem
  },
  props: {
    isResponsive: {
      type: Boolean,
      default: false
    },
    formData: {
      type: Object,
      default: form
    },
    visible: {
      type: Boolean,
      default: true
    }
  },
  data: function data() {
    return {
      show: true,
      listBoxSearchTemplate: {
        sn: 0,
        iconName: "search",
        visible: true
      }
    };
  },
  methods: {
    linkClick: function linkClick(row) {
      this.$emit("linkClick", row);
    },
    onEmbededContextMenu: function onEmbededContextMenu(e) {
      alert("cannot save");
      return false;
    },
    pdfUrl: function pdfUrl(fileUrl) {
      // return //`${fileUrl}#zoom=85&toolbar=0&navpanes=0`
      /* const pageToConvertAsImage = 1;
      const isBase64 = true;
      const res = fromPath(filePath, options).convert(pageToConvertAsImage, isBase64);
      console.log("res: ", res) */

      //`${fileUrl}#zoom=85&toolbar=0&navpanes=0`

      return "".concat(fileUrl, "#zoom=85&toolbar=0&navpanes=0");
    },
    onToggle: function onToggle(event) {
      this.$emit("onToggle", event);
    },
    onFileSelected: function onFileSelected(event) {
      //let video = document.getElementById('video-preview');
      console.log("event: ", event);
      var files = event.target.files;
      var selectedFile = files[0];
      console.log("selectedFile: ", selectedFile);
      this.$emit("onFileSelected", {
        selectedFile: selectedFile
      });
      this.$emit("onFileSelected2", {
        selectedFile: selectedFile
      });
    },
    onColorSelected: function onColorSelected() {},
    onQSelectItemValueChanged: function onQSelectItemValueChanged(qSelect) {
      this.$emit(qSelect.actionName, qSelect);
    },
    ClickAction: function ClickAction(actionName, formData) {
      var context = this;
      if (context.btnDisabled === true) context.btnDisabled = false;else context.btnDisabled = true;
      this.$emit(actionName, formData);
    },
    qInputTemplateAction: function qInputTemplateAction(payload) {
      this.$emit("qInputTemplateAction", payload);
    },
    qListTemplateAction: function qListTemplateAction(payload) {
      this.$emit("qListTemplateAction", payload);
    },
    qListAddItemAction: function qListAddItemAction(payload) {
      this.$emit("qListAddItemAction", payload);
    },
    handleAddEditableTable: function handleAddEditableTable(payload) {
      this.$emit(payload.handleAddName, payload);
    },
    handleSubmitEditableTable: function handleSubmitEditableTable(payload) {
      this.$emit(payload.tableVM.handleSubmitName, payload);
    },
    handleEditEditableTable: function handleEditEditableTable(payload) {
      this.$emit(payload.tableVM.handleEditName, payload);
    },
    handleDeleteEditableTable: function handleDeleteEditableTable(payload) {
      this.$emit(payload.tableVM.handleDeleteName, payload);
    },
    ListItemHandler: function ListItemHandler(actionName, qListItem) {
      console.log("actionName: ", actionName);
      console.log("qListItem: ", qListItem);
      this.$emit(actionName, qListItem);
    },
    disableRightclick: function disableRightclick(evt) {
      var context = this;
      console.log("evt");
      if (evt.button == 2) {
        return false;
      }
    }
  },
  created: function created() {
    var context = this;
    //window.addEventListener("mousedown", this.disableRightclick);
  }
};